.profile-dropdown,
.theme-dropdown {
  margin-bottom: 10px; }
  .profile-dropdown .Select-control,
  .theme-dropdown .Select-control {
    border-radius: 3px;
    background-color: #ffffff;
    border: solid 1px #e6ecf0;
    height: 43px; }
  .profile-dropdown.Select.is-open > .Select-control,
  .theme-dropdown.Select.is-open > .Select-control {
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
    border-color: #e6ecf0; }
  .profile-dropdown .Select-placeholder,
  .theme-dropdown .Select-placeholder {
    font-family: "Roboto", Helvetica, sans-serif;
    font-size: 16px;
    font-weight: bold;
    color: black; }
  .profile-dropdown .Select-placeholder,
  .profile-dropdown.Select--single > .Select-control .Select-value,
  .theme-dropdown .Select-placeholder,
  .theme-dropdown.Select--single > .Select-control .Select-value {
    line-height: 43px; }
  .profile-dropdown .Select-menu-outer,
  .theme-dropdown .Select-menu-outer {
    border-bottom-right-radius: 3px;
    border-bottom-left-radius: 3px;
    border: solid 1px #e6ecf0;
    border-top: none;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05); }
  .profile-dropdown.Select.is-focused:not(.is-open) > .Select-control,
  .theme-dropdown.Select.is-focused:not(.is-open) > .Select-control {
    border-color: #7181ff; }



